
























































.menu-wrapper {
	position: relative;
	display: inline-block;
	margin: 0 8px;
}
.select_button {
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	padding: 8px 16px;
	font-family: inherit;
	font-size: 14px;
	font-weight: 500;
	color: #fff;
	text-transform: uppercase;
	cursor: pointer;
	background: rgba(255, 255, 255, 0);
	border: none;
	border-radius: 2px;

	&:hover {
		background: #ba2350;
	}

	.rotate {
		transform: rotate(180deg);
	}
}
.select {
	position: absolute;
	right: 0;
	left: 0;
	z-index: 99;
	display: none;
	padding: 0;
	margin: 0;
	color: black;
	list-style-type: none;
	cursor: pointer;
	background: #fff;
	border-top: none;
	box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2),
		0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12);
}
.expand + .select {
	display: block;
}
.option {
	padding: 16px;
	font-size: 16px;
	font-weight: 400;

	&:hover {
		background: #e0e0e0;
	}
}
.selected {
	color: var(--primaryColor);
}
